import React, {Component} from 'react';

import './about.css'




class about extends Component{
	render(){
		return (
			<div className="about-wrapper">
			<div className="about-me">
					<h1> About Me.</h1>
				</div>
			<section class="container">
				<div className="text-heading">
					<p>Some history </p>
				</div>
				<div className="spacer-div"/>
				<div className="text-body">
					<p>I started coding around the age of 10. The first language I used was TI-Basic the language for the popular TI-84 graphing calculator.</p>
					<p>I decided to persue a career in web design and am currently getting ceritfied in full stack development from OpenClassrooms.</p>
				</div>
			</section>
			<section class="container">
				<div className="text-heading">
					<p>Current Goals </p>
				</div>
				<div className="spacer-div"/>
				<div className="text-body">
					<p>My current Objective is to get ceritfied in full stack web development and start my career as a web developer.</p>
					
				</div>
			</section>
			<section class="container">
				<div className="text-heading">
					<p>Future Outlook</p>
				</div>
				
				<div className="spacer-div"/>
				<div className="text-body">
					<p>My long term goal is to live out of San Fran and do back end web development there.</p>
					
				</div>
			</section>
			
           </div>
			
		);
	}
}











export default about;