import {faFacebook,faTwitter,faGithubAlt,faLinkedin} from "@fortawesome/free-brands-svg-icons";
import {faEnvelope} from "@fortawesome/free-solid-svg-icons"





const Footer = () => (
    <footer className="footer">
        <a href="http://www.facebook.com"><FontAwesomeIcon icon={faFacebook}size="2x"/></a>  
	    <a href="http://www.twitter.com"><FontAwesomeIcon icon={faTwitter}size="2x"style={{color:'aqua'}}/></a>
		<a href="https://www.linkedin.com/in/ahummel610/"><FontAwesomeIcon icon={faLinkedin}size="2x"/></a>
		<a href="http://www.github.com/lysander695"><FontAwesomeIcon icon={faGithubAlt}size="2x" style={{color:'grey'}}/></a>
        <a href="mailto:hummelal610@gmail.com"><FontAwesomeIcon icon={faEnvelope}size="2x" style={{color:'grey'}}/></a>
    </footer>
  );


export default Footer;