import React, {Component} from 'react';
import colorado from './colorado.jpg';
import watch from './watch.jpg';
import soon from './soon.jpg';

import './cards.css';

import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';



class card extends Component{
	


	render(){
		return (
			<div className='projectCards'>
				<div className='spacer'/>
				<Card style={{ width: '24rem' }}>
					<Card.Img variant="top" src={colorado} />
						<Card.Body>
							<Card.Title>Custom Wordpress Site</Card.Title>
							<Card.Text>
								A Fully Customized Wordpress site hosted online.
							</Card.Text>
							<a href="http://dev-coloradochaletsandcaviar.pantheonsite.io/" target="_blank" rel="noopener noreferrer"> <Button  variant="dark">Check It Out</Button> </a>
						</Card.Body>
				</Card>
				<div className='spacer'/>
				<Card style={{ width: '24rem' }}>
					<Card.Img variant="top" src={watch} />
						<Card.Body>
							<Card.Title>Custom Bootstrap site</Card.Title>
							<Card.Text>
								Custom boostrap sight with css styling.
							</Card.Text>
							<a href="https://github.com/Lysander695/film-festive-site" target="_blank" rel="noopener noreferrer"> <Button variant="dark">Check It Out</Button></a>
						</Card.Body>
				</Card>
				<div className='spacer'/>
				<Card style={{ width: '24rem' }}>
				
					<Card.Img variant="top" src={soon} />
						<Card.Body>
							<Card.Title>Work in progress</Card.Title>
							<Card.Text>
								Future project.
							</Card.Text>
							<Button variant="dark">Comming Soon</Button>
						</Card.Body>
				</Card>
				<div className='spacer'/>
				<Card style={{ width: '24rem' }}>
					<Card.Img variant="top" src={soon} />
						<Card.Body>
							<Card.Title>Work in progress</Card.Title>
							<Card.Text>
								Future project.
							</Card.Text>
							<Button variant="dark">Comming Soon</Button>
						</Card.Body>
				</Card>
				<div className='spacer'/>
			</div>
		);
	}
}











export default card;