import React, {Component} from 'react';
import Form from './form';


import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faFacebook,faTwitter,faGithubAlt,faLinkedin} from "@fortawesome/free-brands-svg-icons";

import './contact.css';


class contact extends Component{
	render(){
		return (
			
				
			
			<div className="contact">
					<header id="contact-header" ><h1>Contact Me</h1></header>
				<div className="contact-info">
					<div className="contact-email">
						<span>Feel Free to contact me at </span>
						<span><a href="mailto:hummelal610@gmail.com"> hummelal610@gmail.com </a></span>
						<p> Or send a message below </p>
					</div>
				
				</div>
				<div className="contact-form">
					<Form />
				</div>
				<div className="social">
					<p>You can also find me on social media </p>
					<a href="http://www.facebook.com"><FontAwesomeIcon icon={faFacebook}size="2x"style={{color:'darkbluew'}}/></a>  
					<a href="http://www.twitter.com"><FontAwesomeIcon icon={faTwitter}size="2x"style={{color:'aqua'}}/></a>
					<a href="https://www.linkedin.com/in/ahummel610/"><FontAwesomeIcon icon={faLinkedin}size="2x"/></a>
					<a href="http://www.github.com/lysander695"><FontAwesomeIcon icon={faGithubAlt}size="2x" style={{color:'grey'}}/></a>
				</div>
				
			</div>
		);
	}
}

export default contact;