import React, {Component} from 'react';
import './intro.css';

import port from './port.jpg';


class intro extends Component{
	render(){
		return (
			
				
			
			<div className="intro">
				<div className='intro-text'>

					<img className="port" src={port} alt="self portrait"/>
					<h1> Hi there, Im Alexander.</h1>
					<p> But most people call me Al.</p>
					<p> I am a pasionate coder who is working on breaking into the web development world.</p>
					<p> It has always been my dream to work in some form of programing job whether it be game design, app development, or web development.</p>
					<p> Please feel free to browse my site or even drop me a message.</p>
				
				</div>
			</div>
		);
	}
}











export default intro;