import React, {Component} from 'react';
import Badge from 'react-bootstrap/Badge';
import ListGroup from 'react-bootstrap/ListGroup';

import './skills.css';







class skills extends Component{
    render(){
        return(
            <div className="skills-wrapper">
                <div className="skills-header">
                    <h1>Skills</h1>
                </div>
                <div className="skills">
                    <div className="spacer" />
                    <div className="skills-list">
                        <h2 className="titles">Programing Languages</h2>
                        <ListGroup>
                            <ListGroup.Item>C++ <Badge variant="secondary">4.5/10</Badge></ListGroup.Item>
                            <ListGroup.Item>Racket <Badge variant="secondary">7/10</Badge></ListGroup.Item>
                            <ListGroup.Item>Java <Badge variant="secondary">5/10</Badge></ListGroup.Item>
                            <ListGroup.Item>Pearl <Badge variant="secondary">8/10</Badge></ListGroup.Item>
                            <ListGroup.Item>JavaScript <Badge variant="secondary">6/10</Badge></ListGroup.Item>
                         <ListGroup.Item>HTML <Badge variant="secondary">8/10</Badge></ListGroup.Item>
                            <ListGroup.Item>CSS <Badge variant="secondary">9/10</Badge></ListGroup.Item>
                        </ListGroup>
                    </div>
                    <div className="skills-list">
                        <h2 className="titles">Software</h2>
                        <ListGroup>
                            <ListGroup.Item>Microsoft Office <Badge variant="secondary">9.5/10</Badge></ListGroup.Item>
                            <ListGroup.Item>Photoshop/Gimp <Badge variant="secondary">6/10</Badge></ListGroup.Item>
                            <ListGroup.Item>Illustrator <Badge variant="secondary">5/10</Badge></ListGroup.Item>
                            <ListGroup.Item>After Effects <Badge variant="secondary">5/10</Badge></ListGroup.Item>
                            <ListGroup.Item>VSCode <Badge variant="secondary">6/10</Badge></ListGroup.Item>
                            <ListGroup.Item>Sublime/Notepad++<Badge variant="secondary">8/10</Badge></ListGroup.Item>
                        
                        </ListGroup>
                    </div>
                    <div className="spacer" />
                </div>
            </div>
        );
    }

}

export default skills;